
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import {mapGetters} from 'vuex'
export default {
  computed: {
    ...mapGetters({
      list: 'getNewProduct'
    })
  },
  data () {
    return {
      height: '',
      sort: false,
      index: 0,
      bar: [{
        text: '',
        key: 'all',
        options: [{
          key: 'all',
          value: ''
        }, {
          key: 'vehicle',
          value: ''
        }, {
          key: 'beauty',
          value: ''
        }, {
          key: 'fishing',
          value: ''
        }, {
          key: 'kitchen',
          value: ''
        }, {
          key: 'bag',
          value: ''
        }]
      }, {
        text: '',
        key: 'default'
      }, {
        text: '',
        key: 'news',
        sort: 'default'
      }, {
        text: '',
        key: 'score',
        sort: 'default'
      }],
      option: ''
    }
  },
  mounted () {
    console.log(document.querySelector('.vux-slider').clientHeight)
    this.height = document.querySelector('.vux-slider').clientHeight + 'px'
  },
  components: {
    Tab,
    TabItem,
    PopupRadio,
    Popup,
    Swiper,
    SwiperItem,
    Scroller,
    XImg
  },
  methods: {
    handleSort () {
      this.sort = true
    },
    handleChangeSort () {
      this.sort = false
    },
    handleChange (index) {
      console.log(this.bar[index])
      this.index = index
    },
    handleSwiper (index) {
      console.log(this.bar[index])
    }
  }
}



